<template>
  <div>
    <b-form>
      <br />
      <b-row class="no-gutters">
        <b-col cols="12">
          <b-button variant="outline-primary" class="px-4 required-btnAny">{{
            $t("Any")
          }}</b-button
          >&nbsp;&nbsp;
          <label class="highLightHeaderLabel">{{ $t("StartMonth") }}</label>
        </b-col>
        <b-col id="monthSelect" cols="10" xl="3" class="mt-3">
          <b-form-select
            @change="onChange($event)"
            v-model="selectedMonth"
            :plain="true"
            :options="monthOptions"
            value-field="item"
            text-field="name"
            disabled-field="notEnabled"
            class="optionClass"
          ></b-form-select>
        </b-col>
      </b-row>
      <br />
      <b-row class="no-gutters">
        <b-col cols="12">
          <b-button variant="outline-primary" class="px-4 required-btnAny">{{
            $t("Any")
          }}</b-button
          >&nbsp;&nbsp;
          <label class="highLightHeaderLabel">{{ $t("ComPanyIcon") }}</label>
        </b-col>
        <b-col cols="12" xl="1" lg="1" class="justifyMobile">
                <div id="thumbnailLogoClient" v-if="imagePath">
                    <img
                      v-if="imagePath"
                      @click="onPickFile()"                      
                      v-bind:src="baseDOMAIN + imagePath"
                      class="thumbnailLogoImage"
                      alt="client_icon"
                    />
                    <input
                      id="fileupload"
                      ref="fileupload"
                      @change="onFileChange"
                      type="file"
                      name="fileupload"
                      hidden
                    />
                    <div class="thumbnailLogo1">
                      <b-button
                        id="fileSelect"
                        @click="onPickFile()"                        
                        class="camCls"
                      >
                      </b-button>
                      <input
                        id="fileupload"
                        ref="fileupload"
                        @change="onFileChange"
                        type="file"
                        name="fileupload"
                        hidden
                      />
                    </div>
                  </div>
                  <div id="thumbnailLogoClient" v-else >
                    <img
                      @click="onPickFile()"                    
                      src="@/assets/browse_image.svg"
                      class="thumbnailLogo"
                      alt="client_icon"
                    />
                    <input
                      id="fileupload"
                      ref="fileupload"
                      @change="onFileChange"
                      type="file"
                      name="fileupload"
                      hidden
                    />
                    <div class="thumbnailLogo1">
                      <b-button
                        id="fileSelect"
                        @click="onPickFile()"                        
                        class="camCls"
                      >
                      </b-button>
                      <input
                        id="fileupload"
                        ref="fileupload"
                        @change="onFileChange"
                        type="file"
                        name="fileupload"
                        hidden
                      />
                    </div>
                  </div>
        </b-col>
        <b-col cols="10" xl="12">
          <p class="text-danger">{{ fileErrorMessage }}</p>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="3" class="ml-3 justifyMobile">
          <b-link
            @click="deleteFile"
            href="javascript:;"
            class="card-link float-left link mt-2"
          >
            <u>{{ $t("DeleteIcon") }}</u>
          </b-link>
        </b-col>
      </b-row>
      <b-row class="mt-4">
        <b-col cols="12" xl="2" class="pr-0">
          <b-button variant="outline-primary" class="required-btnAny">{{
            $t("Any")
          }}</b-button
          >&nbsp;&nbsp;
          <label class="highLightHeaderLabel">{{ $t("SERVICEAGENCY") }}</label>
        </b-col>
        <b-col cols="11" xl="3">
          <b-checkbox
            :checked="inputAgencyOption"
            @change="updateInputAgencyFlag"
            class="highLightHeaderLabel"
            >{{ $t("CheckFlag") }}</b-checkbox
          >
        </b-col>
      </b-row>
    </b-form>
  </div>
</template>
<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { mapGetters } from "vuex";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";
import { Config } from "@/shared/config";
export default {
  data() {
    return {
      baseDOMAIN: Config.apiDomain,
      imagePath: "",
      inputAgencyOption: "",
      text: "",
      file: "",
      fileName: "",
      files: [],
      fileErrorMessage: "",
      monthOptions: [
        { item: "1", name: "1月" },
        { item: "2", name: "2月" },
        { item: "3", name: "3月" },
        { item: "4", name: "4月" },
        { item: "5", name: "5月" },
        { item: "6", name: "6月" },
        { item: "7", name: "7月" },
        { item: "8", name: "8月" },
        { item: "9", name: "9月" },
        { item: "10", name: "10月" },
        { item: "11", name: "11月" },
        { item: "12", name: "12月" },
      ],
      clientotherinfono: "",
      selectedMonth: "",
      currentMonth: new Date().getMonth() + 1,
      autoproposal: "",
      autoproposalDate: "",
      cntBaseInfoDate: "",
      OtherInfo: [],
    };
  },
  computed: {
    // ...mapGetters({
    //   user_details: "getUserDetails",
    //   client_details: "getClientData",
    // }),
  },
  validataions: {},
  mounted() {
    this.getClientOtherInfoData();
  },
  methods: {
    onPickFile() {
      //this.$refs.fileInput.click()
      document.getElementById("fileupload").click();
    },
    /**
     * @description function to fetch client other info data based on the clientno
     * @name getClientOtherInfoData
     */
    async getClientOtherInfoData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      if (data.success) {
        this.OtherInfo = data.data;
        if (this.OtherInfo.length > 0) {
          let responseData = data.data;
          let sortData = this._.sortBy(responseData, "client_other_info_no");
          this.clientotherinfono = sortData[0].client_other_info_no;
          this.selectedMonth = sortData[0].year_start_month;
          this.autoproposal = sortData[0].auto_proposal;
          this.autoproposalDate = sortData[0].auto_proposal_dt;
          this.inputAgencyOption = sortData[0].input_agency_option;
          if (sortData[0].icon_file_name != "null") {
            this.fileName = sortData[0].icon_file_name;
            this.imagePath = sortData[0].icon_path;
          } else {
            this.imagePath = "";
          }
        }
      }
    },
    /**
     * @description function update inputAgencyFlag
     * @name updateInputAgencyFlag
     */
    async updateInputAgencyFlag() {
      if (this.OtherInfo.length > 0) {
        this.inputAgencyOption = !this.inputAgencyOption;
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MASTERUPDATEMCLINETOTHERINFO,
          {
            data: [
              {
                client_no: localStorage.getItem("client_account_no"),
                year_start_month: this.selectedMonth,
                auto_proposal: this.autoproposal,
                auto_proposal_dt: this.autoproposalDate,
                del_flg: false,
                input_agency_option: this.inputAgencyOption,
                update_id: localStorage.getItem("client_account_no"),
                update_dt:
                  new Date().getFullYear() +
                  "-" +
                  this.currentMonth +
                  "-" +
                  new Date().getDate() +
                  " " +
                  new Date().getHours() +
                  ":" +
                  new Date().getMinutes() +
                  ":" +
                  new Date().getSeconds(),
              },
            ],
            conditions: [
              { key: "client_other_info_no", value: this.clientotherinfono },
            ],
          }
        );
        if (data.success) {
          console.log("Success", data);
        }
      } else {
        this.createOtherInfo();
      }
    },
    /**
     * @description function to update or create the Data on change of the month dropdown
     * @name onChange
     */
    async onChange() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length > 0) {
          this.updateOtherInfo();
        } else {
          this.createOtherInfo();
        }
      }
    },
    /**
     * @description function to update the ClientOtherInfo details
     * @name updateOtherInfo
     */
    async updateOtherInfo() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATEMCLINETOTHERINFO,
        {
          data: [
            {
              client_no:localStorage.getItem("client_account_no"),
              year_start_month: this.selectedMonth,
              auto_proposal: this.autoproposal,
              auto_proposal_dt: this.autoproposalDate,
              del_flg: false,
              update_id:localStorage.getItem("client_account_no"),
              update_dt:
                new Date().getFullYear() +
                "-" +
                this.currentMonth +
                "-" +
                new Date().getDate() +
                " " +
                new Date().getHours() +
                ":" +
                new Date().getMinutes() +
                ":" +
                new Date().getSeconds(),
            },
          ],
          conditions: [
            { key: "client_other_info_no", value: this.clientotherinfono },
          ],
        }
      );
      if (data.success) {
        console.log("Success", data);
      }
    },
    /**
     * @description function to create the clientOtherInfo details
     * @name createOtherInfo
     */
    async createOtherInfo() {
      this.inputAgencyOption = !this.inputAgencyOption;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERCREATEMCLIENTOTHERINFO,
        [
          {
            client_no:localStorage.getItem("client_account_no"),
            year_start_month: this.selectedMonth,
            auto_proposal: false,
            auto_proposal_dt:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            icon_path: "null",
            icon_file_name: "null",
            del_flg: false,
            input_agency_option: this.inputAgencyOption,
            create_id:localStorage.getItem("client_account_no"),
            create_dt:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
            update_id: localStorage.getItem("client_account_no"),
            update_dt:
              new Date().getFullYear() +
              "-" +
              this.currentMonth +
              "-" +
              new Date().getDate() +
              " " +
              new Date().getHours() +
              ":" +
              new Date().getMinutes() +
              ":" +
              new Date().getSeconds(),
          },
        ]
      );
      if (data.success) {
        console.log("Success", data);
      }
    },
    /**
     * @description function to upload the file
     * @name onFileChange
     */
    onFileChange(event) {
      let fileSize = 0;
      for (var i = 0; i < event.target.files.length; i++) {
        var ext = event.target.files[i].name.split(".").pop();
        fileSize += Math.round(event.target.files[i].size / 1024);
        if (
          ext == "jpg" ||
          ext == "png" ||
          ext == "jpeg" ||
          ext == "JPG" ||
          ext == "PNG" ||
          ext == "JPEG"
        ) {
          if (fileSize > 1024) {
            this.fileErrorMessage = this.$t("MAXICONSIZE");
          } else {
            for (let i = 0; i < event.target.files.length; i++) {
              this.files.push(event.target.files[i]);
            }
            this.fileName = event.target.files[0].name;
            this.fileUpload();
          }
        } else {
          this.fileErrorMessage = this.$t("UPLOADEXCECUTABLEFILES");
        }
        fileSize += Math.round(event.target.files[i].size / 1024);
      }
    },
    /**
     * @description function to check for create or update the file based on clientOtherInfoNo
     * @name fileUpload
     */
    async fileUpload() {
      this.fileErrorMessage = "";
      var formData = new FormData();
      for (let i = 0; i < this.files.length; i++) {
        formData.append("attachments[]", this.files[i]);
      }

      formData.append("client_no", localStorage.getItem("client_account_no"));
      formData.append("update_id", localStorage.getItem("client_account_no"));
      formData.append(
        "update_dt",
        new Date().getFullYear() +
          "-" +
          this.currentMonth +
          "-" +
          new Date().getDate()
      );
      formData.append("year_start_month", this.selectedMonth);

      formData.append(
        "auto_proposal_dt",
        new Date().getFullYear() +
          "-" +
          this.currentMonth +
          "-" +
          new Date().getDate() +
          " " +
          new Date().getHours() +
          ":" +
          new Date().getMinutes() +
          ":" +
          new Date().getSeconds()
      );
      formData.append("auto_proposal", false);
      formData.append("create_id", 1);
      formData.append("del_flg", false);
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length > 0) {
          this.updateUpload();
        } else {
          const { data } = await BasicInfoApis.postAPI(
            endPoint.EVALUATIONCREATECLIENTOTHERINFO,
            formData
          );
          if (data.success) {
            console.log("Success", data);
          }
        }
      }
    },
    /**
     * @description function to update the upload file path And Icon
     * @name updateUpload
     */
    async updateUpload() {
      var formData = new FormData();
      for (let i = 0; i < this.files.length; i++) {
        formData.append("attachment", this.files[i]);
      }
      formData.append("client_other_info_no", this.clientotherinfono);
      formData.append("update_id", localStorage.getItem("client_account_no"),);
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONUPDATECLIENTOTHERINFOICON,
        formData
      );
      if (data.success) {
        this.getClientOtherInfoData();
        console.log("Success", data);
      }
    },
    /**
     * @description function to update file Path And Icon
     * @name prefectureData
     */
    async deleteFile() {
      this.fileName = "";
      this.files = [];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATEMCLINETOTHERINFO,
        {
          data: [
            {
              client_no: localStorage.getItem("client_account_no"),
              year_start_month: this.selectedMonth,
              auto_proposal: this.autoproposal,
              auto_proposal_dt: this.autoproposalDate,
              del_flg: false,
              icon_path: "null",
              icon_file_name: "null",
              update_id: localStorage.getItem("client_account_no"),
              update_dt:
                new Date().getFullYear() +
                "-" +
                this.currentMonth +
                "-" +
                new Date().getDate() +
                " " +
                new Date().getHours() +
                ":" +
                new Date().getMinutes() +
                ":" +
                new Date().getSeconds(),
            },
          ],
          conditions: [
            { key: "client_other_info_no", value: this.clientotherinfono },
          ],
        }
      );
      if (data.success) {
        this.getClientOtherInfoData();
        this.$refs.fileupload.value = null;
        console.log("Success", data);
      }
    },
  },
};
</script>
